import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

import { useSelector, useDispatch } from 'react-redux';

export default function BasicSelect() {
  

  const  centreTypeId = useSelector(state => state.centreTypeId);
  const dispatch = useDispatch();
  const handleChange = (e, val) => {
    console.log(e.target.value);
     
      return dispatch(
          {
              type: 'set_centretype',
              centreTypeId: e.target.value
          }
      )
  };

 

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Centre type</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={centreTypeId}
          label="Centretype"
          onChange={handleChange}
        >
          <MenuItem value={1}>Metro or Strategic</MenuItem>
          <MenuItem value={2}>Metro</MenuItem>
          <MenuItem value={3}>Strategic</MenuItem>
          <MenuItem value={4}>Large Local Centre (&#8805; 10k sqm of retails)</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
}