import React  from 'react';
import data from './data.json'
import { useSelector, useDispatch } from 'react-redux';
import { fontSize } from '@mui/system';



const Narravties = ()=>{

    const  centreTypeId = useSelector(state => state.centreTypeId);
    const datasets  = data[centreTypeId.toString()].datasets;
    const percentage = datasets.map( item=>{
         if (item.label==='At least once'){
             return item.data[0]

        }
    });
    return (
        <p style = {{color:'#231F20', fontSize:'14px'}}>
            <p>This map shows 30 minutes catchments by public transport and walking to the selected centres with service frequency on a weekday (7-9am, Monday, 04/11/19).</p>
            <q style= {{fontSize:'16px'}}><b>{percentage}%</b> of dwellings across Greater Sydney Region has 30 minutes access to its nearest centre with at least a trip in the AM peak.</q>
            </p>
    )
}

export default Narravties;