setDefaultCredentials(credentials);




const MAPBOX_ACCESS_TOKEN = 'pk.eyJ1IjoiYnNuYXlhbiIsImEiOiJja3Ewczd3MW4wN3Z3MnVvNDNsenlzNnFsIn0.f8nk8yrxIeCKOlzIy1b-Pg'

export const POLYGON_COLORS ={
    COLOR_YES: [0,0,0,0],
    COLOR_NO :[248,15,54],
    COLOR_GREEN :[16,196,118]
  }
  
const AccessMap = ()=> {

  const  centreTypeId = useSelector(state => state.centreTypeId);

  const [data ,setData] = useState([]);

const query ='SELECT ST_X(ST_Centroid(the_geom)) x, ST_Y(ST_CENTROID(the_geom)) y,district from districtboundary';
useEffect( ()=>{
  const getData = async ()=> {
    
    const response  = await axios.get(`https://gsc-admin.carto.com/api/v2/sql?q=${encodeURIComponent(query)}`);
    setData(response.data.rows.map(elm=>{
      const d= {text:elm.district,position:[elm.x,elm.y]}
      //console.log(d);
      return {text:elm.district,position:[elm.x,elm.y]}
    })
    )
    return response;
    
  }
  getData();

},[query])

console.log('laoding..');


  
const texLayer = new TextLayer({
  id: 'text-layer',
  data,
  pickable: false,
  sizeMinPixels:2,
  sizeMaxPixels:15,

  getSize: 20,
  getAngle: 0,
  getTextAnchor: 'middle',
  getAlignmentBaseline: 'center',
  getBackgroundColor:[255,255,255],
  getColor:[255,255,255]
});

   
     
  
    const layerAccess = new CartoLayer({
      id:'access-uni',
      type: MAP_TYPES.QUERY,
      data: `SELECT cartodb_id,the_geom_webmercator,centre_type,frequency,sym_code,percentage FROM "gsc-admin".thirtyminutescatchemntsbyfrequency_rings where centre_typeid =${centreTypeId} order by frequency desc`,
    
      getFillColor: object =>{
        
        return [...COLOR_ACCESS[object.properties.sym_code],200]
      },
      getLineColor: object =>COLOR_ACCESS[object.properties.sym_code],
      lineWidthMinPixels: 1,
      pickable: true,
      
    });
    
    const whereClasue = centreTypeId===1?'centretypeid <=3': 'centretypeid = '+centreTypeId;

    const styleDict = {
      2: {
        fc:[213,82,39,200],
        ec:[255,255,255]

      },
      3: {
        fc:[50,46,132,200],
        ec:[255,255,255]

      },
      4: {
        fc:[0,0,0,200],
        ec:[255,255,255]

      },
    }
    
    const layerCentres = new CartoLayer({
      id:'centres',
      type: MAP_TYPES.QUERY,
      data: `SELECT cartodb_id,the_geom_webmercator,centrename,centretype,centretypeid FROM "gsc-admin".centres_metro_strategic_largelocal where ${whereClasue}`,
    
      getFillColor: object => styleDict[object.properties.centretypeid].fc,
       
      getLineColor: object =>styleDict[object.properties.centretypeid].ec,
      radiusScale:1,
      pointRadiusMinPixels: 1,
      getRadius: object=>1/object.properties.centretypeid *1000,
      pickable: true,
      lineWidthMinPixels:1
     
      
    });

    const layerDistrict = new CartoLayer({
      id:'district',
      type: MAP_TYPES.QUERY,
      data: `select the_geom_webmercator, district from "gsc-admin".districtboundary `,
    
      getFillColor: [0,0,0,0],
      getLineColor: [102, 96, 100],
      lineWidthMinPixels: 1.2,
      
      pickable: false,
      
    });

    
  
    const getTooltip = ({object}) => {
      if (!object) return false;
      const {centre_type,frequency,sym_code,percentage,centrename,centretype} = object.properties;

      let html = '<table>';
      html += centre_type?`<tr><td>Catchment type:</td><td>${centre_type}</td></tr>`:'';
      html += sym_code?`<tr ><td>Service frequency:</td><td>${sym_code}</td></tr>`:'';
      // html += percentage?`<tr><td>Percentage:</td><td>${percentage}%</td></tr>`:'';
      html += centretype?`<tr><td>Centre type:</td><td>${centretype}</td></tr>`:'';
      html += centrename?`<tr><td>Name:</td><td>${centrename}</td></tr>`:'';
      html +='</table>';
      
      return {
        html: html,
        
        
        style:{
          
          fontSize: '0.8em',
          
         
        }
      }
    };

  
    return (
      <div>
       
        <DeckGL
          width="100%"
          height="100%"
          initialViewState={INITIAL_VIEW_STATE}
          getTooltip = {getTooltip}
          controller={true}
          layers={[layerAccess,layerDistrict,layerCentres,texLayer]}
        >
          <StaticMap
            reuseMaps
            mapStyle= "mapbox://styles/bsnayan/ckwww9fx40ebi14qc5357zqwt"//{BASEMAP.DARK_MATTER}//"mapbox://styles/mapbox/dark-v9"
            mapboxApiAccessToken= {MAPBOX_ACCESS_TOKEN}
            preventStyleDiffing
          />
        </DeckGL>
      </div>
    );
  }


  export default AccessMap;